.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  transition: 450ms -webkit-transform;
  transition: 450ms transform;
  transition: 450ms transform, 450ms -webkit-transform;
  font-size: 0;
  white-space: wrap;
  margin: 70px 0px;
  padding-bottom: 10px;
  max-width: 1250px;
}

@media only screen and (max-width: 991px) {
  .cards {
    margin-bottom: 30px;
    margin-top: 30px;
  }
}
